p, ul, button {
  margin: 0;
  padding: 0;
}

.btnToUp {
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  position: fixed;
  bottom: 80px;
  right: 10px;
}

.iconToUp {
  width: 50px;
  height: 50px;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  border-radius: 50%;
  transition: background-color .25s cubic-bezier(.4, 0, .2, 1), color .25s cubic-bezier(.4, 0, .2, 1);
}

.iconToUp:hover, .iconToUp:focus {
  fill: #fff;
  background-color: #ff6b08;
  border: 1px solid #000;
}

.load-more-btn {
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  border-color: #fff;
  border-radius: 5px;
  margin: 20px auto;
  padding: 5px;
  font-family: Roboto;
  font-size: 20px;
  font-style: normal;
  font-weight: 500;
  line-height: 24px;
  transition: background-color .25s cubic-bezier(.4, 0, .2, 1), color .25s cubic-bezier(.4, 0, .2, 1);
  display: block;
}

.load-more-btn:hover, .load-more-btn:focus {
  background-color: #ff6b08;
  border: 1px solid #000;
}

.js-cards {
  flex-wrap: wrap;
  gap: 16px;
  margin-top: 60px;
  display: flex;
}

.js-card {
  width: 395px;
  margin-bottom: 32px;
  font-family: Roboto;
  font-size: 20px;
  font-style: normal;
  font-weight: 500;
  line-height: 24px;
  list-style: none;
}

.js-card-img {
  width: 395px;
}

.js-movie-title {
  max-width: 395px;
  color: #000;
}

.js-movie-genres {
  color: #ff6b08;
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.js-movie-reiting {
  color: #fff;
  background: #ff6b01;
  border-radius: 5px;
  padding: 3px 8px;
  font-family: Roboto;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
  line-height: 14px;
}

.js-movie-descr {
  flex-direction: column;
  justify-content: space-between;
  padding: 2px;
  display: flex;
}

.js-on-card {
  cursor: pointer;
  border: none;
}

.js-card:hover, .js-card:focus {
  box-shadow: 0 5px 10px rgba(107, 35, 35, .24);
}

@media screen and (max-width: 767px) {
  .js-cards {
    gap: 20px;
  }

  .js-card, .js-card-img {
    width: 280px;
  }
}

@media screen and (max-width: 1279px) {
  .js-cards {
    gap: 32px;
  }

  .js-card {
    width: 336px;
    margin-bottom: 0;
    font-size: 12px;
    line-height: 16px;
  }

  .js-card-img {
    width: 336px;
  }
}

